import "./Home.css"
import {Link, useNavigate} from "react-router-dom";
import {useState} from "react";

function Login(){
    const [name, setName] = useState("");
    const [password, setPassword] = useState("");
    const navigate = useNavigate();

    function onChangeName(event){
        setName(event.target.value);
    }

    function onChangePassword(event){
        setPassword(event.target.value);
    }

    const onLogin = async () => {
        const json = await (
            await fetch(
                `/mono/login?username=${name}&password=${password}`
            )
        ).json();
        loginCallback(json);
    }

    function loginCallback(json){
        if(json.result){
            alert("Login Success")
            navigate('/home');
        }else {
            alert("Confirm Your Name and Password")
        }

    }

    return (
        <div className="form-signin">
            <h1 className="h3 mb-3 fw-normal">
                Kukie
            </h1>
            <div className="form-floating">
                <input
                    type="id"
                    className="form-control"
                    id="floatingIdInput"
                    placeholder="ID"
                    name="username"
                    value={name}
                    onChange={onChangeName}
                />
                <label htmlFor="floatingInput">
                    ID
                </label>
            </div>
            <div className="form-floating">
                <input
                    type="password"
                    className="form-control"
                    id="floatingPasswordInput"
                    placeholder="Password"
                    name="password"
                    value={password}
                    onChange={onChangePassword}
                />
                <label htmlFor="floatingInput">
                    Password
                </label>
            </div>
            <button onClick={onLogin} className="w-100 btn btn-lg btn-primary" type="submit">Sign in</button>
            <hr/>
            <p><Link to={`/register`}>Register</Link></p>
        </div>
    );

}

function Register(){
    const [name, setName] = useState("");
    const [password, setPassword] = useState("");
    const navigate = useNavigate();

    function onChangeName(event){
        setName(event.target.value);
    }

    function onChangePassword(event){
        setPassword(event.target.value);
    }

    const onRegister = async () => {
        const json = await (
            await fetch(
                `/mono/registerUser?username=${name}&password=${password}`
            )
        ).json();
        registerCallback(json);
    }

    function registerCallback(json){
        alert("Register Success");
        navigate('/');
    }

    return (
        <div className="form-signin">
        <h1 className="h3 mb-3 fw-normal">Register</h1>
        <p className="textP">Name</p>
        <input
            type="text"
            id="registName"
            className="form-control"
            name="Name"
            value={name}
            onChange={onChangeName}
        />
        <p className="textP">Password</p>
        <input
            type="password"
            id="registPassword"
            className="form-control"
            name="password"
            value={password}
            onChange={onChangePassword}
        />
        <br/><br/>
        <button onClick={onRegister} className="w-100 btn btn-lg btn-primary" type="submit">Sign Up</button>

        <hr/>

        </div>
    )
}

function Home(){
    return (
        <div>
            <h3>Welcome Portal</h3>
            <img className="homeImg" src="/static/image/home.png"/>
        </div>
    )
}

export {
    Login,
    Register,
    Home,
};